@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
.hero-container{background-color:#fffffe;height:70vh;overflow:hidden}.hero-container .title,.hero-container .subtitle,.hero-container .txt{max-width:80% !important}@media screen and (max-width: 414px){.hero-container .title,.hero-container .subtitle,.hero-container .txt{max-width:100%}}.hero-container .subtitle{font-size:1.4em !important;font-weight:normal}.hero-container .subtitle-container{width:100%;margin:2em 0.5em 1em 0.5em}@media screen and (max-width: 414px){.hero-container .subtitle-container{max-width:100%}}.hero-container img{border-radius:1em}

.search-container{justify-content:center;background:#fffffe;padding:2.5em 2em;margin:2.5em}.search-container .button{text-transform:uppercase;margin-top:25px}.search-container form{width:75%;align-items:center !important}.search-container form label{font-family:'Montserrat', sans-serif;font-size:20px;color:#2d334a}.search-container form input{min-width:250px !important;margin-right:2.5em;padding:0.2em 0.8em !important;border-radius:0.5em !important}

.available-container{background:#bae8e8;padding:2em 15% 2em 15%}.available-container .img-container{max-height:60vh !important;overflow:hidden;border-radius:.4em;position:relative}.available-container img,.available-container .img-container{height:100%;width:100%}.available .title-container{background:white;padding:.8em;margin-top:-10%;position:relative;bottom:-5%;width:90%;box-shadow:10px 10px 5px -7px rgba(0,0,0,0.28);border-radius:.4em}.available .img-container{max-height:60vh !important;overflow:hidden;border-radius:.4em;position:relative}.available img,.available .img-container{width:100%;height:100%}.available .subtitle-pet{font-family:"open Sans", sans-serif;font-size:1.2em;color:#2d334a}.available .publication-card{min-width:220px;min-height:240px}

.steps{min-height:100vh;display:flex;align-items:center;justify-content:center;grid-area:steps}.steps .steps-inner{height:100%}.steps .subtitle-pet{font-family:"Montserrat", sans-serif;color:#272343;text-align:center;font-weight:bold;text-transform:uppercase;font-size:2.6em}.steps .txt-pet{font-family:"Open Sans", sans-serif;margin-top:1em;color:#2d334a;font-size:1.3em}.steps .img-pet{width:100%;margin-bottom:2em}

.discover-container img{max-height:380px;height:auto}.discover-container .card-container{margin:2em 0 2em 0}.discover-container h2{font-family:"Open Sans", sans-serif;font-size:1.4em;font-weight:bold;color:#272343}.discovery-container{margin:2em 0 2em 0}

